@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;1,400;1,600&display=swap);
body, html {
  margin: 0;
  width: 100%;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fffffc;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.overlay-container {
  width: 200%;
  position: fixed;
  top: 0;
  overflow: hidden;
  z-index: 2;
  background-color: rgba(0, 8, 20, 0.96);
  -webkit-animation: menuChange 200ms linear;
  animation: menuChange 200ms linear;
}

.overlay-appear {
  height: 100vh;
  opacity: 1;
}

.overlay-disappear {
  height: 0vh;
  opacity: 0;
}

.overlay-main {
  left: 0;
  transition: all 250ms linear;
}

.overlay-sub {
  left: -100%;
  transition: all 250ms linear;
}

.icon-container {
  transition: none;
}

.overlay-menus {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.icon-container.main-menu {
  width: 45%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: center;
  padding: 20px;
  left: 0;
  margin: 0 5% 0 0;
}

.icon-container.sub-menu {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  width: 45%;
  max-width: 800px;
  left: -100%;
  margin: 0 5% 0 50%;
}

img.exit, img.back {
  width: 25px;
  padding: 5px;
  cursor: pointer;
}

.overlay-main-menu,.overlay-submenu {
  width: 50%;
  height: 100%;
  padding: 30px 0;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.overlay-main-menu img {
  width: 22px;
  padding: 10px 5px;
}

.menu-item {
  padding: 5px;
  margin: 10px;
}

.text-menu:first-child {
  margin-left: 25px;
}

.menu-item p {
  font-family: 'Lato', sans-serif;
  color: rgba(255, 195, 0, 1);
  letter-spacing: 0.1em;
  font-size: 36px;
  font-weight: bold;
  cursor: pointer;
}

.text-menu {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes menuChange {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

@keyframes menuChange {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

.header-container {
  background-color: #000814;
  padding: 15px 50px;
}

.header-menu {
  margin: 5px auto;
  max-width: 1300px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
}

.header-logo {
  padding: 0 45px 0 0;
}

.header-logo img {
  max-width: 90px;
  opacity: 1;
}

.header-list {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  padding: 15px 10px 10px;
}

.header-list:hover .nav-item p {
  color: rgba(255, 195, 0, 0.6);
}

.nav-item {
  margin: 0 20px;
  padding: 5px;
}

.nav-item p, .dropdown p {
  font-family: 'Lato', sans-serif;
  letter-spacing: 0.1em;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  padding: 10px;
  margin: 0;
}

.nav-item p {
  transition: all 100ms linear;
  color: rgba(255, 195, 0, 0.95);
}

#menu:hover, #submenu1:hover, #submenu2:hover, #events:hover, #about:hover {
  color: rgba(255, 195, 0, 0.95);
}

.dropdown {
  margin: 0 20px;
}

.dropdown-content {
  margin: 5px;
  opacity: 0;
  visibility: hidden;
  top: 60px;
  position: absolute;
  background-color: #1f1f1f;
  padding: 15px 15px;
  transition: all 150ms linear;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  border-radius: 2px;
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  visibility: visible;
  display: block;
  top: 80px;
  opacity: 1;
}

.dropdown-content p {
  color: rgba(255, 195, 0, 0.7);
  margin: 5px auto;
  transition: all 150ms linear;
}

.dropdown:hover #menu {
  color: rgba(255, 195, 0, 1);
}

.menu-icon {
  display: none;
  padding: 5px;
}

.menu-icon img {
  max-width: 25px;
  opacity: 1;
  cursor: pointer;
}

@media only screen and (max-width: 700px) {
  .header-container {
    padding: 10px 25px;
  }
  .header-menu {
    justify-content: space-between;
  }
  .header-logo {
    padding: 0 25px 0 0;
  }

  .header-logo img {
    max-width: 55px;
  }
  .header-list {
    display: none;
  }
  .menu-icon {
    display: flex;
  }
}

.main-container {
  width: 100%;
  height: 700px;
}

.main-text {
  font-family: 'Open Sans', sans-serif;
  height: 700px;
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #fffffc;
}

.main-text h1 {
  font-size: 75px;
  margin: 25px 0;
  text-shadow: 2px 2px 8px black;
}

.main-text p {
  font-size: 30px;
  text-shadow: 2px 2px 4px black;
  margin: 0 10px 10px;
}

@media only screen and (max-width: 600px) {
  .main-container {
    height: 640px;
  }
  .main-text {
    height: 600px;
  }
  .main-text h1 {
    font-size: 60px;
    margin: 15px;
  }
  .main-text p {
    font-size: 24px;
    margin: 5px;
  }
}

.beans-container {
  margin: 50px auto 0;
  padding: 50px 0;
}

.beans-h2-image {
  font-family: 'Lato', sans-serif;
  max-width: 930px;
  padding: 0 5px 0 25px;
  margin: 5px auto;
  display: flex;
  position: relative;
  flex-flow: row nowrap;
  justify-content: space-between;
}

.beans-h2 {
  padding: 5px;
  max-width: 450px;
  display: flex;
  align-items: flex-start;
  transition: all 400ms linear;
}

.beans-h2-showing {
  opacity: 1;
  margin-left: 0px;
}

.beans-h2-hidden {
  opacity: 0;
  margin-left: -30px;
}

.beans-image {
  padding: 5px;
  margin: 40px 0 0;
  transition: all 400ms linear;
}

.beans-image-showing {
  opacity: 1;
}

.beans-image-hidden {
  opacity: 0;
}

.beans-image img {
  max-width: 420px;
}

.beans-container h2 {
  color: #000814;
  font-size: 70px;
  margin: 0 0 15px;
  line-height: 1.4em;
  border-left: 8px solid rgba(255, 195, 0, 1);
  padding: 0 0 10px 25px;
}

.beans-subtext-upper-buffer {
  height: 5px;
  width: 40%;
  margin: 65px auto 0;
  max-width: 350px;
  border-bottom: 1px solid rgba(255, 195, 0, 1);
  transition: all 500ms linear;
}

.beans-upper-buffer-showing {
  opacity: 1;
}

.beans-upper-buffer-hidden {
  opacity: 0;
}

.beans-subtext-lower-buffer {
  height: 5px;
  width: 60%;
  margin: 0 auto 25px;
  max-width: 550px;
  border-top: 1px solid rgba(255, 195, 0, 1);
}

.beans-subtext {
  margin: 0 auto;
  max-width: 750px;
  width: 80%;
  padding: 5px;
  text-align: center;
  color: #000814;
  transition: all 400ms linear;
  font-family: 'Lato', sans-serif;
}

.beans-subtext h3 {
  font-size: 22px;
}

.beans-subtext-showing {
  opacity: 1;
}

.beans-subtext-hidden {
  opacity: 0;
}

.beans-subtext p {
  font-size: 20px;
}

@media only screen and (max-width: 1100px) {
  .beans-container {
    margin: 25px auto;
  }
  .beans-h2-image {
    justify-content: center;
  }
  .beans-h2 {
    max-width: 350px;
  }
  .beans-container h2 {
    font-size: 55px;
    line-height: 1.2em;
    padding: 0 0 10px 20px;
  }
  .beans-image {
    padding: 5px;
    margin: 30px 0 0;
  }
  .beans-image img {
    max-width: 325px;
    padding: 0 0 0 25px;
  }
  .beans-subtext {
    width: 85%;
  }
  .beans-subtext p {
    font-size: 18px;
  }
}

@media only screen and (max-width: 800px) {
  .beans-container {
    margin: 10px auto;
  }
  .beans-h2-image {
    justify-content: center;
  }
  .beans-h2 {
    max-width: 275px;
  }
  .beans-container h2 {
    font-size: 40px;
    line-height: 1.2em;
    padding: 0 0 5px 20px;
  }
  .beans-image {
    padding: 5px;
    margin: 20px 0 0;
  }
  .beans-image img {
    max-width: 230px;
    padding: 0;
  }
  .beans-subtext-upper-buffer {
    margin: 45px auto 0;
  }
}

@media only screen and (max-width: 600px) {
  .beans-container {
    margin: 10px auto;
    padding: 30px 0;
  }
  .beans-h2-image {
    justify-content: center;
    flex-flow: column nowrap;
    align-items: center;
    padding: 5px;
  }
  .beans-h2 {
    max-width: 300px;
  }
  .beans-container h2 {
    font-size: 40px;
    line-height: 1.2em;
    padding: 0 30px 5px 20px;
  }
  .beans-image {
    padding: 5px;
    margin: 0;
  }
  .beans-image img {
    max-width: 230px;
    padding: 0;
  }
  .beans-subtext-upper-buffer {
    margin: 30px auto 0;
  }
  .beans-subtext p {
    font-size: 16px;
  }
}

.countries-cards-container {
  margin: 0px auto;
  width: 90%;
  max-width: 1100px;
  padding: 5px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.countries-header {
  text-align: center;
  padding: 5px 5px 10px;
  font-family: 'Lato', sans-serif;
}

.countries-header h3 {
  font-size: 22px;
  color: #000814;
  margin: 22px 0 10px;
}

.country-container {
  width: 275px;
  height: 275px;
  border-radius: 100%;
  border: 2px solid rgba(0, 8, 20, 0.4);
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  opacity: 1;
  margin: 10px 5px 0;
  box-shadow: 0px 0px 3px #403d35;
}

.country-container:hover {
  opacity: 0.8;
  border: 2px solid rgba(0, 8, 20, 0.7);
}

.country-inner {
  overflow: hidden;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  border-radius: 100%;
}

.stripe {
  width: 60px;
  height: 100%;
  transition: all 200ms linear;
}

.country-text {
  padding: 20px 10px 20px 15px;
  transition: all 200ms linear;
}

.country-text-not-hover {
  color: rgba(255, 195, 0, 1);
  text-shadow: 1px 1px 1px rgba(0, 8, 20, 0.7);
}

.country-text-hover {
  color: #000814;
  text-shadow: 0;
}

.country-map img {
  transition: all 300ms linear;
}

.country-map-showing img {
  opacity: 1;
  width: 120px;
  margin: 15px 5px 5px 25px;
  transition: all 300ms linear;
}

.country-map-hidden img {
  opacity: 0;
  width: 0;
  margin: 0 5px 0 25px;
  transition: all 200ms linear;
}

.country-text-map {
  display: flex;
  flex-flow: column nowrap;
  padding: 10px 20px;
}

.country-text-map h4 {
  margin: 0;
  font-size: 20px;
  font-family: 'Lato', sans-serif;
}

.spec-line {
  margin: 15px 0 0;
}

.country-specs p {
  margin: 5px 5px 5px 15px;
}

.country-specs-hidden {
  display: none;
  opacity: 0;
}

.bold {
  font-weight: bold;
}

.costa-rica, .kenya, .sumatra {
  background-color: rgba(176, 176, 176, 0);
}

.costa-rica-hover, .kenya-hover, .sumatra-hover {
  background: linear-gradient(-45deg, rgba(255, 196, 0, 1), rgba(255, 196, 0, 0.4));
}

.costa-rica-stripe, .kenya-stripe, .sumatra-stripe {
  background-color: #ffc300;
  opacity: 1;
}

.costa-rica-stripe-hover, .kenya-stripe-hover, .sumatra-stripe-hover {
  background-color: #000814;
  opacity: 1;
}

