.drink-menu-link-container {
  text-align: center;
  font-family: 'Lato', sans-serif;
  margin: 30px auto 50px;
}

.drink-menu-button {
  background-color: rgba(255, 196, 0, 1);
  width: 180px;
  margin: 5px auto;
  padding: 15px 0;
  border-radius: 30px;
  cursor: pointer;
  transition: all 200ms linear;
}

.drink-menu-button:hover {
  background-color: rgba(255, 196, 0, 0.7);
}

.drink-menu-link-container a {
  font-size: 18px;
