}

.bakery-container {
  background-color: #000814;
  width: 100%;
  padding: 40px 0;
}

.bakery-header {
  padding: 5px;
  margin: 25px auto;
  width: 600px;
  text-align: center;
  line-height: 1.4em;
}

.bakery-header h2 {
  margin: 0;
}

.baking {
  font-family: 'Lato', sans-serif;
  font-size: 70px;
  margin: 15px 0;
  color: #fffffc;
  transition: all 350ms linear;
  line-height: 1.4em;
}

.baking-top-showing, .baking-top-hidden {
  margin-left: 20px;
}

.baking-top-showing, .baking-bottom-showing {
  opacity: 1;
}

.baking-top-hidden, .baking-bottom-hidden {
  opacity: 0;
}

.fresh {
  font-size: 70px;
  font-style: italic;
  transition: all 400ms linear;
}

.fresh-hidden {
  opacity: 0;
  padding-left: 30px;
  padding-right: 0;
  color: #fffffc;
}

.fresh-showing {
  opacity: 1;
  padding-left: 0;
  padding-right: 30px;
  color: #ffc300;
}

.bakery-subtext {
  width: 550px;
  padding: 5px;
  margin: 5px auto;
  text-align: center;
}

.bakery-subtext p {
  color: #fffffc;
  font-family: 'Lato', sans-serif;
  font-size: 20px;
}

.popular-pastries {
  text-align: center;
  color: #fffffc;
  padding: 25px 0 0;
  transition: all 300ms linear;
}

.popular-pastries h3 {
  font-size: 22px;
  font-family: 'Lato', sans-serif;
}

.bakery-images-container {
  width: 80%;
  max-width: 925px;
  margin: 20px auto 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.bakery-type-container {
  border-radius: 3px;
  width: 350px;
  height: 350px;
