  transition: all 300ms linear;
}

.croissants-container-showing, .tartlets-container-showing, .muffins-container-showing, .donuts-container-showing {
    opacity: 1;
}

.croissants-container-hidden, .tartlets-container-hidden, .muffins-container-hidden, .donuts-container-hidden {
