.events-container {
  width: 100%;
  height: 600px;
  background-image: url(/coffee-shop/static/media/musicStage.3e2e27e1.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.events-text-container {
  width: 50%;
  max-width: 800px;
  height: 550px;
  font-family: 'Lato', sans-serif;
  color: black;
  margin: 0 auto;
  padding: 40px 0 0;
}

.events-header {
  padding: 5px;
  margin: 0px auto 5px;
  padding: 50px 0 0;
  width: 370px;
  text-align: center;
}

.events-header h2 {
  color: #fffffc;
  font-size: 62px;
  margin: 0 0 40px;
  line-height: 1.2em;
  text-shadow: 2px 2px 8px black;
}

.events-container img {
  opacity: 0.7;
}

.events-subtext-upper-buffer {
  height: 5px;
  width: 325px;
  margin: 15px auto 5px;
  border-bottom: 1px solid rgba(255, 195, 0, 1);
}

.events-subtext-lower-buffer {
  height: 5px;
